h1,
h2 {
  margin: 0;
  padding: 0; }

.container {
  background-color: #222;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #EEE;
  height: 100vh; }

.divider {
  background-color: #bc17e6;
  border: 0;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  margin-top: 30px;
  margin-bottom: 40px;
  width: 25%; }

.profileImage {
  margin-bottom: 5px;
  height: 230px;
  width: 230px;
  border: 1px solid #bc17e6;
  box-shadow: 3px 3px 3px #bc17e6;
  border-radius: 50%; }
  .profileImage img {
    width: 230px;
    border-radius: 50%; }

.name {
  z-index: 10;
  font-size: 1.25em; }

.about {
  z-index: 10;
  margin-top: 0;
  font-size: 0.7em;
  margin-bottom: 30px;
  color: #999; }

.socialSites {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 20px; }

.projects {
  background-color: chartreuse; }

.socialLinks {
  position: absolute;
  bottom: 30px;
  display: flex;
  flex-wrap: wrap;
  width: 50%;
  justify-content: space-around; }
  .socialLinks > a {
    justify-content: center; }
    .socialLinks > a img {
      height: 40px; }

@media (max-width: 767px) {
  .socialLinks {
    width: 75%; } }

@media (max-height: 700px) {
  .container {
    justify-content: flex-start;
    padding-top: 15px; } }
