.link {
  color: #FFF;
  display: flex;
  text-decoration: none;
  font-weight: 500;
  text-shadow: none;
  margin: 5px 0px;
  text-align: center; }
  .link::before, .link::after {
    color: #FFF;
    display: inline-block;
    box-sizing: border-box;
    opacity: 0;
    transition: transform 0.35s, opacity 0.35s; }
  .link::before {
    content: '';
    color: #bc17e6;
    transform: translateX(5px) scaleY(0.5);
    height: 100%;
    width: 5px;
    position: relative;
    border-left: 1.5px solid #bc17e6;
    border-top: 1.5px solid #bc17e6;
    border-bottom: 1.5px solid #bc17e6; }
  .link::after {
    content: '';
    color: #bc17e6;
    transform: translateX(-5px) scaleY(0.5);
    height: 100%;
    width: 5px;
    position: relative;
    border-right: 1.5px solid #bc17e6;
    border-top: 1.5px solid #bc17e6;
    border-bottom: 1.5px solid #bc17e6; }
  .link:hover::before, .link:focus::before {
    transform: translate(-5px) scaleY(1);
    opacity: 1; }
  .link:hover::after, .link:focus::after {
    transform: translateX(5px) scaleY(1);
    opacity: 1; }
